body {
  margin: 0;
  padding: 0;
  font-family: 'Fira Sans Condensed', 'Arial', sans-serif;
  font-size: 18px;
  line-height: 1.4em;
  font-weight: 300;
  color: #000000;
  background-color: #fffcfa;

  @media (max-width: 1024px) {
    font-size: 16px;
  }

  @media (max-width: 540px) {
    font-size: 18px;
  }
}

.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  clip: rect(0 0 0 0);
  overflow: hidden;
}

.site-container {
  box-sizing: border-box;
  width: 100%;
  max-width: 1104px;
  padding: 0 40px;
  margin: 0 auto;

  @media (max-width: 1024px) {
    padding: 0 21px;
  }

  @media (max-width: 540px) {
    padding: 0 12px;
  }
}
