.curtain-types {
  $root: &;

  margin-top: 80px;

  &__content-wrapper {
    margin-top: 10px;

    @media (max-width: 1024px) {
      margin-top: 48px;
    }
  }

  &__row,
  &__columns-wrapper {
    margin: 50px 0;

    &:first-child {
      margin-top: 0;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__row {
    display: flex;

    @media (max-width: 1024px) {
      margin-left: -21px;
      margin-right: -21px;
    }

    @media (max-width: 540px) {
      margin-left: 0;
      margin-right: 0;
      flex-direction: column;
    }

    #{$root}__image {
      @media (max-width: 1024px) {
        width: 69%;
        height: auto;
      }

      @media (max-width: 540px) {
        width: 100%;
        margin-bottom: 24px;
      }
    }
  }

  &__columns-wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 40px;

    @media (max-width: 540px) {
      grid-template-columns: 1fr;
      grid-gap: 50px;
    }
  }

  &__column {
    #{$root}__image {
      margin-bottom: 16px;

      @media (max-width: 1024px) {
        width: 100%;
        height: auto;
      }
    }
  }

  &__description {
    text-align: center;

    &--right {
      margin-left: 24px;

      @media (max-width: 1024px) {
        padding-right: 21px;
      }
    }

    &--left {
      margin-right: 24px;

      @media (max-width: 1024px) {
        padding-left: 21px;
      }
    }

    @media (max-width: 540px) {
      order: 2;
      margin: 0;
      padding: 0;
    }
  }

  &__link {
    border-bottom: 1px dashed rgba(#a82332, 0.3);
    color: #a82332;
    text-decoration: none;

    &:hover {
      color: #ff0000;
      border-bottom-color: #ff0000;
    }
  }

  &__image {
    display: block;
  }
}
