.site-header {
  $root: &;

  position: fixed;
  top: 0;
  left: 0;
  z-index: 4;
  width: 100%;
  border-bottom: 1px solid transparent;
  transition-property: background-color, border-bottom-color;
  transition-duration: 0.3s;

  @media (max-width: 1024px) {
    position: absolute;
  }

  &--fixed {
    background-color: #ffffff;
    border-bottom-color: rgba(0, 0, 0, 0.1);

    #{$root}__schedule {
      display: none;
    }
  }

  &__container {
    position: relative;
    padding-top: 16px;
    padding-bottom: 16px;
    display: flex;
    align-items: center;

    @media (max-width: 1024px) {
      align-items: flex-start;
    }

    @media (max-width: 540px) {
      align-items: center;
    }
  }

  &__logo-image {
    margin-right: 36px;

    @media (max-width: 540px) {
      width: 92px;
      height: auto;
    }
  }

  &__nav-button {
    display: none;
    position: relative;
    margin-left: 24px;
    width: 26px;
    height: 26px;
    padding: 0;
    border: none;
    background-color: transparent;

    @media (max-width: 1024px) {
      display: block;
    }

    @media (max-width: 540px) {
      margin-left: auto;
    }

    &::before,
    &::after,
    #{$root}__button-line {
      content: '';
      position: absolute;
      left: 0;
      width: 100%;
      height: 3px;
      background-color: #ce3141;
    }

    &::before {
      top: 0;
    }

    &::after {
      bottom: 0;
    }
  }

  &__button-line {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }

  &__nav-wrapper {
    @media (max-width: 1024px) {
      display: none;
      position: absolute;
      top: 64px;
      right: 21px;
      z-index: 2;
      box-sizing: border-box;
      max-width: 240px;
      padding: 20px 26px;
      background-color: #ffffff;
      border-radius: 2px;
      box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.18), 0 -1px 0 0 rgba(0, 0, 0, 0.03);
    }

    &--opened {
      @media (max-width: 1024px) {
        display: block;
      }
    }
  }

  &__nav-list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;

    @media (max-width: 1024px) {
      flex-direction: column;
    }
  }

  &__nav-item {
    margin-right: 20px;

    @media (max-width: 1024px) {
      margin-right: 0;
      margin-bottom: 16px;
    }

    &:last-child {
      margin-right: 0;

      @media (max-width: 1024px) {
        margin-bottom: 0;
      }
    }
  }

  &__nav-link {
    position: relative;

    @media (max-width: 1024px) {
      border-bottom: 1px dashed rgba(0, 0, 0, 0.3);
    }

    &::after {
      content: '';
      position: absolute;
      top: 100%;
      left: 0;
      width: 100%;
      border-bottom: 1px dashed rgba(0, 0, 0, 0.3);

      @media (max-width: 1024px) {
        content: none;
      }
    }

    &:hover {
      color: #ce3141;

      &::after {
        border-bottom: 1px dotted #ce3141;
      }
    }
  }

  &__phone-link,
  &__nav-link {
    color: inherit;
    text-decoration: none;
  }

  &__phone-link {
    @media (max-width: 1024px) {
      border-bottom: 1px dashed rgba(0, 0, 0, 0.3);
    }
  }

  &__contacts-wrapper {
    position: relative;
    margin-left: auto;

    @media (max-width: 540px) {
      margin-left: 0;
    }
  }

  &__phone-wrapper,
  &__schedule {
    margin: 0;
  }

  &__phone-wrapper {
    font-size: 21px;
    line-height: 1em;
    font-weight: 400;

    @media (max-width: 540px) {
      font-size: 18px;
    }
  }

  &__schedule {
    position: absolute;
    top: calc(100% + 10px);
    right: 0;
    text-align: right;

    @media (max-width: 540px) {
      display: none;
    }
  }
}
