.intro-block {
  padding-top: 160px;
  padding-bottom: 145px;
  font-size: 21px;
  text-align: center;
  background-image: url('images/intro-bg@2x.jpg');
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;

  @media (max-width: 540px) {
    padding-top: 96px;
    padding-bottom: 80px;
    font-size: 18px;
    text-align: left;
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;

    @media (max-width: 540px) {
      align-items: flex-start;
    }
  }

  &__title,
  &__description {
    margin: 0;
  }

  &__title {
    margin-bottom: 100px;
    font-family: 'Noto Serif';
    font-size: 53px;
    line-height: 1em;
    font-weight: 300;

    @media (max-width: 1024px) {
      margin-bottom: 32px;
      max-width: 80%;
      line-height: 1.2em;
    }

    @media (max-width: 540px) {
      margin-bottom: 64px;
      font-size: 26px;
    }
  }

  &__description {
    margin-bottom: 35px;
  }

  &__services-list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;

    @media (max-width: 540px) {
      flex-direction: column;
    }
  }

  &__service-item {
    position: relative;
    width: 100%;
    max-width: 170px;
    margin-right: 25px;
    padding-top: 30px;

    @media (max-width: 540px) {
      width: auto;
      max-width: 100%;
      padding-top: 0;
      padding-left: 56px;
      margin-right: 0;
      margin-bottom: 25px;
    }

    &:last-child {
      margin-right: 0;
    }

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 22px;
      background-repeat: no-repeat;
      background-position: top center;
      background-size: contain;

      @media (max-width: 540px) {
        width: 40px;
        height: 100%;
        background-position: center center;
      }
    }

    &--design {
      @media (max-width: 540px) {
        order: 2;
        margin-bottom: 0;
      }
    }

    &--cost::before {
      background-image: url('images/cost-icon.svg');
    }

    &--design::before {
      background-image: url('images/design-icon.svg');
    }

    &--time::before {
      background-image: url('images/time-icon.svg');
    }
  }

  &__link {
    margin-top: 65px;
    padding: 16px 45px;
    display: block;
    font-weight: 400;
    text-decoration: none;
    color: #ffffff;
    background-color: #ce3141;
    border-radius: 2px;
    transition: background-color 0.3s;

    @media (max-width: 540px) {
      margin-top: 48px;
      padding-left: 24px;
      padding-right: 24px;
    }

    &:hover {
      background-color: #e84356;
    }
  }
}
