@font-face {
  font-family: 'Noto Serif';
  src: url('fonts/NotoSerif-400.woff2') format('woff2'),
    url('fonts/NotoSerif-400.woff') format('woff');
  font-display: swap;
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Fira Sans Condensed';
  src: url('fonts/FiraSansCondensed-300.woff2') format('woff2'),
    url('fonts/FiraSansCondensed-300.woff') format('woff');
  font-display: swap;
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: 'Fira Sans Condensed';
  src: url('fonts/FiraSansCondensed-400.woff2') format('woff2'),
    url('fonts/FiraSansCondensed-400.woff') format('woff');
  font-display: swap;
  font-weight: 400;
  font-style: normal;
}
