.site-page {
  min-height: 100vh;
  display: flex;
  flex-direction: column;

  &__main {
    flex-grow: 1;
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 13px;
    padding-bottom: 13px;
    font-size: 21px;
    line-height: 30px;

    @media (max-width: 1024px) {
      font-size: 19px;
      line-height: 25px;
    }

    @media (max-width: 540px) {
      font-size: 20px;
    }

    &::after {
      width: 70px;
    }
  }

  &__paragraph {
    margin: 0;
    margin-bottom: 10px;

    @media (max-width: 1024px) {
      margin-bottom: 8px;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__title,
  &__description {
    margin: 0 auto;
    max-width: 480px;
    text-align: center;

    @media (max-width: 540px) {
      padding-left: 12px;
      padding-right: 12px;
    }
  }

  &__title,
  &__subtitle {
    position: relative;
    font-family: 'Noto Serif';
    font-weight: 400;

    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 50%;
      height: 1px;
      transform: translateX(-50%);
      background-color: rgba(#ce3141, 0.5);
    }
  }

  &__title {
    margin-bottom: 16px;
    padding-bottom: 24px;
    font-size: 40px;
    line-height: 50px;

    @media (max-width: 1024px) {
      font-size: 35px;
    }

    @media (max-width: 540px) {
      padding-bottom: 18px;
      font-size: 26px;
      line-height: 34px;
    }

    &::after {
      width: 140px;

      @media (max-width: 540px) {
        width: 90px;
      }
    }
  }

  &__description {
    @media (max-width: 1024px) {
      max-width: 580px;
    }
  }
}
