.note-block {
  position: relative;
  box-sizing: border-box;
  margin: 0;
  min-height: 177px;
  padding: 40px 70px;
  display: flex;
  background-image: url('images/note-bg@2x.png');
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;

  @media (max-width: 1024px) {
    min-height: 168px;
  }

  @media (max-width: 768px) {
    flex-direction: column;
    min-height: 0;
    padding: 88px 21px 24px 21px;
    margin-left: -21px;
    margin-right: -21px;
    border-top: 3px solid #e897a0;
    border-bottom: 3px solid #e897a0;
    text-align: center;
    background-image: none;
    background-color: #ffffff;
  }

  @media (max-width: 540px) {
    padding: 88px 12px 24px 12px;
    margin-left: -12px;
    margin-right: -12px;
  }

  &__title {
    flex-shrink: 0;
    max-width: 190px;
    font-family: 'Noto Serif';
    font-size: 21px;
    line-height: 1.5em;
    font-weight: 400;

    @media (max-width: 1024px) {
      font-size: 19px;
    }

    @media (max-width: 768px) {
      position: relative;
      padding-bottom: 13px;
      margin-bottom: 13px;
      max-width: 100%;
    }

    @media (max-width: 540px) {
      font-size: 21px;
    }

    &::after {
      @media (max-width: 768px) {
        content: '';
        position: absolute;
        bottom: 0;
        left: 50%;
        width: 70px;
        height: 1px;
        transform: translateX(-50%);
        background-color: rgba(#ce3141, 0.5);
      }
    }
  }

  &__description {
    margin-top: 5px;
    margin-left: 16px;
    max-width: 570px;

    @media (max-width: 1024px) {
      margin-top: 4px;
      margin-left: 48px;
      max-width: 100%;
    }

    @media (max-width: 768px) {
      margin: 0;
    }
  }

  &::before,
  &::after {
    content: '';
    position: absolute;

    @media (max-width: 1024px) {
      content: none;
    }

    @media (max-width: 768px) {
      right: auto;
      content: '';
    }
  }

  &::before {
    z-index: 2;
    background-repeat: no-repeat;
    background-size: contain;

    @media (max-width: 768px) {
      left: 50%;
      transform: translateX(-50%);
    }
  }

  &::after {
    top: 40px;
    right: 70px;
    background-color: #ffa3ab;

    @media (max-width: 768px) {
      top: 24px;
      left: 50%;
    }
  }

  &--lifetime {
    &::before {
      top: 60px;
      right: 100px;
      width: 50px;
      height: 60px;
      background-image: url('images/lifetime-icon.svg');

      @media (max-width: 768px) {
        top: 28px;
        width: 43px;
        height: 50px;
      }
    }

    &::after {
      width: 57px;
      height: 38px;

      @media (max-width: 768px) {
        width: 47px;
        height: 32px;
      }
    }
  }

  &--shapes {
    &::before {
      top: 58px;
      right: 80px;
      width: 43px;
      height: 60px;
      background-image: url('images/shape-icon.svg');

      @media (max-width: 768px) {
        top: 28px;
        width: 38px;
        height: 50px;
      }
    }

    &::after {
      width: 31px;
      height: 47px;

      @media (max-width: 768px) {
        width: 28px;
        height: 42px;
      }
    }
  }
}
