.offer-block {
  margin-top: 80px;
  background-image: url('images/offer-bg@2x.jpg');
  background-repeat: no-repeat;
  background-size: 1920px auto;
  background-position: center center;

  @media (max-width: 1024px) {
    background-size: cover;
  }

  @media (max-width: 540px) {
    padding-bottom: 56px;
    background-size: auto 60%;
    background-position: bottom left;
  }

  &__content-wrapper {
    display: flex;
    align-items: center;

    @media (max-width: 1024px) {
      padding-left: 0;
    }

    @media (max-width: 540px) {
      padding: 0;
      flex-direction: column;
    }
  }

  &__image {
    display: block;
    margin-right: 40px;

    @media (max-width: 1024px) {
      width: 47%;
      height: auto;
    }

    @media (max-width: 768px) {
      width: 43%;
      margin-right: 16px;
    }

    @media (max-width: 540px) {
      width: 100%;
      margin-right: 0;
      margin-bottom: 56px;
    }
  }

  &__text-wrapper {
    padding: 72px 0;
    max-width: 420px;
    font-weight: 400;
    color: #ffffff;

    @media (max-width: 1024px) {
      padding: 0;
      max-width: 380px;
    }

    @media (max-width: 540px) {
      padding: 0 12px;
    }
  }

  &__title {
    position: relative;
    margin: 0;
    margin-bottom: 16px;
    padding-bottom: 24px;
    font-family: 'Noto Serif';
    font-size: 40px;
    line-height: 1em;
    font-weight: 400;

    @media (max-width: 1024px) {
      padding-bottom: 16px;
      margin-bottom: 20px;
      max-width: 240px;
      line-height: 1.3em;
    }

    @media (max-width: 540px) {
      max-width: 100%;
      padding-bottom: 24px;
      font-size: 26px;
      line-height: 1em;
    }

    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 63px;
      height: 1px;
      background-color: #ffffff;
    }
  }

  &__description {
    margin: 0;
    margin-bottom: 24px;
  }

  &__services-list {
    margin: 0;
    margin-bottom: 32px;
    padding: 0;
    list-style: none;
    display: flex;
    justify-content: space-between;

    @media (max-width: 540px) {
      flex-direction: column;
      justify-content: flex-start;
    }
  }

  &__service-item {
    position: relative;
    padding-top: 30px;

    @media (max-width: 540px) {
      padding-top: 0;
      padding-left: 56px;
      margin-bottom: 25px;
    }

    &:last-child {
      margin-bottom: 0;
    }

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 22px;
      background-repeat: no-repeat;
      background-position: top left;
      background-size: contain;

      @media (max-width: 540px) {
        width: 40px;
        height: 100%;
        background-position: center center;
      }
    }

    &--cost::before {
      background-image: url('images/cost-icon-light.svg');
    }

    &--design::before {
      background-image: url('images/design-icon-light.svg');
    }

    &--time::before {
      background-image: url('images/time-icon-light.svg');
    }
  }

  &__bottom-wrapper {
    display: flex;
    align-items: center;

    @media (max-width: 540px) {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &__link {
    margin-right: 20px;
    padding: 16px 24px;
    text-decoration: none;
    color: #90121f;
    border-radius: 2px;
    background-color: #ffffff;
    transition-property: background-color, color;
    transition-duration: 0.3s;

    &:hover {
      color: #ffffff;
      background-color: #c41515;
    }
  }

  &__schedule {
    margin: 0;

    @media (max-width: 540px) {
      margin-top: 10px;
    }
  }
}
